const defaultConfig = {
    "hood-base-color": "black",
    "crown-color": "black",
    "brow-color": "white",
    "bridge-color": "white",
    "nose-color": "black",
    "muzzle-color": "white",
    "muzzle-lip-color": "white",
    "chin-color": "black",
    "ear-a-color": "white",
    "ear-b-color": "black",
    "ear-c-color": "black",
    "muzzle-a-color": "white",
    "muzzle-b-color": "white",
    "muzzle-button": "black",
    "muzzle-style": "default", // ["default", "fur", "added-default", "added-fur"]
    "muzzle-assoicate": "default", // ["default", "mustache", "fur"]
    "eyebrow-style": "default", // ["default", "dou", "frame"]
    "glass-hole": false,
    "ear-hole": false
};

const colorItems = [
    "hood-base-color",
    "crown-color",
    "brow-color",
    "bridge-color",
    "nose-color",
    "muzzle-color",
    "muzzle-lip-color",
    "chin-color",
    "ear-a-color",
    "ear-b-color",
    "ear-c-color",
    "muzzle-a-color",
    "muzzle-b-color",
    "muzzle-button"
];

const styleItems = [
    "muzzle-style",
    "muzzle-assoicate",
    "eyebrow-style"
];

const checkItems = [
    "glass-hole",
    "ear-hole"
];

const colorList = [
    "red",
    "orange",
    "yellow",
    "green",
    "tiffany",
    "light-blue",
    "blue",
    "brown",
    "pupple",
    "pink",
    "white",
    "gray",
    "black",
    "texture-black",
    "metal"
];

const styleList = [
    "default",
    "fur",
    "added-default",
    "added-fur",
    "mustache",
    "dou",
    "frame"
];

function encodeColor(config) {
    let colorStr = ''
    colorItems.forEach((i) => {
        let r = colorList.indexOf(config[i]);
        if (r < 0) {
            r = 12;
            throw new Error('Unknown Color: ' + i + ' ' + config[i]);
        }
        colorStr += r.toString(16);
    })
    return colorStr;
}

function encodeStyle(config) {
    let styleStr = ''
    styleItems.forEach((i) => {
        let r = styleList.indexOf(config[i]);
        if (r < 0) {
            r = 0;
            throw new Error('Unknown Style: ' + i + ' '  + config[i]);
        }
        styleStr += r.toString(16);
    })
    return styleStr;
}

function encodeCheck(config) {
    let check = 0
    checkItems.forEach((i) => {
        check = check * 2 + (config[i] ? 1 : 0);
    })
    return check.toString(16);
}

function encode(config) {
    try {
        return (encodeColor(config) + "_" + encodeStyle(config) + "_" + encodeCheck(config)).toUpperCase();
    } catch (error){
        console.log('错误', error);
        // alert('未知错误！');
    }
    
}

function decodeColor(str) {
    if (str.length !== colorItems.length) {
        return {}
    }
    let colorConfig = {}
    colorItems.forEach((k, i) => {
        const colorIndex = parseInt(str[i], 16);
        if (colorIndex < colorList.length) {
            colorConfig[k] = colorList[colorIndex];
        }
    })
    return colorConfig;
}

function decodeStyle(str) {
    if (str.length !== styleItems.length) {
        return {}
    }
    let styleConfig = {}
    styleItems.forEach((k, i) => {
        const styleIndex = parseInt(str[i], 16);
        if (styleIndex < styleList.length) {
            styleConfig[k] = styleList[styleIndex];
        }
    })
    return styleConfig;
}

function decodeCheck(str) {
    let check = parseInt(str, 16);
    let checkConfig = {}
    checkItems.reverse().forEach((k) => {
        checkConfig[k] = check % 2 === 1 ? true : false
        check = Math.floor(check / 2)
    });
    return checkConfig;
}

function decode(str) {
    try {
        let [colorStr, styleStr, checkStr] = str.split('_')
        const colorConfig = decodeColor(colorStr);
        const styleConfig = decodeStyle(styleStr);
        const checkConfig = decodeCheck(checkStr);
        return {...colorConfig, ...styleConfig, ...checkConfig};
    } catch (error) {
        console.log('错误', error);
        // alert('未知错误！');
    }
}

module.exports = {
    encode,
    decode,
    colorList,
    defaultConfig
};
